/*Date range picker*/
.daterangepicker.dropdown-menu{
  width:auto;
  max-height: none;
  min-width: auto;
  padding-bottom:50px;
  visibility: visible;
  opacity: 1;
  border:0;
  }
.daterangepicker_input{
  display: none;
  }
.daterangepicker.show-calendar .calendar{
  padding-bottom: 5px;
  margin-bottom: 5px;
  }
.daterangepicker td.today{
  color:#26bec9;
  }
.daterangepicker td.off.available{
  color:#333;
  }
.daterangepicker td.active, .daterangepicker td.off.available.active, .daterangepicker td.active:hover{
  background:#26bec9 !important;
  color:#fff;
  display: table-cell;
  }
.daterangepicker td.disabled, .daterangepicker option.disabled{
  text-decoration:line-through;
  cursor: default;
  color:#eaeaea !important;
  }
.daterangepicker .ranges{
  border-top:1px solid #eee;
  width:100%;
  margin:0;
  padding:10px 15px 10px;
  position:absolute;
  bottom:0;
  right:0;
  background:#fff;
  text-align: right;
  }
.daterangepicker.dropdown-menu.single{/*單一日曆的Daterangepicker*/
  padding-bottom: 0px;
  }  
.daterangepicker.dropdown-menu.single.show-calendar .calendar{/*單一日曆的Daterangepicker*/
  border-bottom: none;
  }  
.daterangepicker select.monthselect, .daterangepicker select.yearselect{
  border: none;
  padding:0 10px;
  width:70px;
  text-align: center;
  background: url("../img/bg_dropdown.svg") #fff;
  background-position: right center;
  background-repeat: no-repeat;
  background-size: 10px;
  appearance: none;
  -webkit-appearance:none;  
  -moz-appearance: none;
  font-size: 1.4rem;
}
.daterangepicker select.monthselect{
    margin-right: 10px;
}
.daterangepicker select.monthselect option, .daterangepicker select.yearselect option{
    padding: 5px 0px;
}