.table-middle > tbody > tr > td {
  vertical-align: middle; }

.tip {
  margin-top: 5px;
  color: #666; }

header {
  height: 60px;
  background-color: #138184;
  padding: 8px 0;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99; }
  header a.logo img {
    height: 44px; }
  header ul.menu {
    float: right;
    text-align: right;
    padding: 0;
    list-style: none; }
    header ul.menu > li {
      float: right; }
      header ul.menu > li > a {
        display: block;
        border-radius: 4px;
        background: transparent;
        color: #fff;
        line-height: 1;
        text-decoration: none;
        padding: 12px 16px;
        margin: 2px 5px; }
        header ul.menu > li > a:hover, header ul.menu > li > a:focus {
          background: rgba(255, 255, 255, 0.1); }
        header ul.menu > li > a i {
          margin-left: 5px; }
        header ul.menu > li > a .user-type {
          background: rgba(255, 255, 255, 0.3);
          padding: 2px 8px;
          margin-right: 4px;
          display: inline; }
      header ul.menu > li > .btn {
        border: 1px solid #fff;
        color: #fff; }

.menu-backdrop {
  position: fixed; }

.burger {
  padding: 10px 0 10px 10px;
  margin-top: -3px;
  float: right;
  cursor: pointer; }
  .burger .bar {
    background-color: #fff;
    border-radius: 1px;
    width: 25px;
    height: 3px;
    margin-bottom: 5px; }

@media (max-width: 991px) {
  header {
    height: 50px;
    transition: left 0.3s; }
    header a.logo img {
      height: 34px; }
  body:not(.login-body) header ul.menu {
    padding: 10px 20px;
    position: fixed;
    top: 0;
    right: -300px;
    width: 300px;
    height: 100%;
    z-index: 1000;
    background-color: #fff;
    text-align: left;
    box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0);
    transition: 0.3s;
    overflow-y: auto; }
    body:not(.login-body) header ul.menu.active {
      right: 0;
      box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.4); }
    body:not(.login-body) header ul.menu .user-type {
      background: #138184;
      display: inline-block; }
    body:not(.login-body) header ul.menu > li {
      display: block;
      float: none;
      margin-top: 15px; }
      body:not(.login-body) header ul.menu > li > a {
        color: #333;
        font-size: 1.6rem;
        padding: 12px 0; }
      body:not(.login-body) header ul.menu > li > .btn {
        color: #138184;
        border-color: #138184;
        margin-top: 15px; }
      body:not(.login-body) header ul.menu > li.dropdown .dropdown-menu {
        position: relative;
        padding-left: 30px;
        float: none !important;
        box-shadow: none; }
        body:not(.login-body) header ul.menu > li.dropdown .dropdown-menu:before {
          display: none; }
    body:not(.login-body) header ul.menu > li.member-dropdown {
      border-bottom: 1px solid #eee;
      margin: 0 -20px;
      padding: 0 20px; }
      body:not(.login-body) header ul.menu > li.member-dropdown .dropdown-menu {
        display: block;
        padding-left: 0; }
        body:not(.login-body) header ul.menu > li.member-dropdown .dropdown-menu > li > a {
          padding: 12px 16px;
          margin: 2px -10px;
          font-size: 1.6rem; }
  body.menu-open {
    left: -300px;
    cursor: pointer; }
    body.menu-open header {
      left: -300px; }
    body.menu-open .menu-backdrop {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; } }

@media (max-width: 767px) {
  body:not(.login-body) header ul.menu {
    right: -70%;
    width: 70%; }
  body.menu-open {
    left: -70%;
    cursor: pointer; }
    body.menu-open header {
      left: -70%; } }

.page-wrap {
  min-height: calc(100vh - 45px);
  padding-top: 90px;
  padding-bottom: 30px; }
  @media (max-width: 767px) {
    .page-wrap {
      min-height: calc(100vh - 107px);
      padding-top: 70px; } }

footer {
  background-color: #555;
  padding: 15px 0;
  height: 45px; }
  @media (max-width: 767px) {
    footer {
      height: auto;
      text-align: center; } }

.copyright {
  color: #fff;
  opacity: 0.5;
  font-size: 1.3rem; }

ul.footer-link {
  text-align: right;
  margin: 0;
  padding: 0; }
  @media (max-width: 767px) {
    ul.footer-link {
      text-align: center;
      margin: 20px 0; } }

ul.footer-link li {
  display: inline-block; }
  ul.footer-link li a {
    color: #fff;
    font-weight: bold;
    padding: 5px; }

.page-aside .aside-box {
  padding: 15px;
  background-color: white;
  border-radius: 6px;
  margin-bottom: 15px; }
  .page-aside .aside-box .title {
    font-size: 1.6rem;
    margin-top: 5px;
    margin-bottom: 15px; }

.page-aside .page-menu {
  list-style: none;
  padding-left: 0;
  background-color: #fff;
  border-radius: 5px;
  margin-top: 20px; }
  .page-aside .page-menu > li > a {
    display: block;
    color: #333;
    padding: 10px 15px; }
    .page-aside .page-menu > li > a:hover {
      background-color: rgba(38, 190, 201, 0.05);
      text-decoration: none; }
  .page-aside .page-menu > li.active a {
    color: #138184;
    position: relative; }
    .page-aside .page-menu > li.active a:before {
      content: '';
      position: absolute;
      top: 50%;
