.page-aside {
    .aside-box {
        padding: 15px;
        background-color: white;
        border-radius: 6px;
        margin-bottom: 15px;

        .title {
            font-size: 1.6rem;
            margin-top: 5px;
            margin-bottom: 15px;
        }
    }
    
    .page-menu {
        list-style: none;
        padding-left: 0;
        background-color: #fff;
        border-radius: 5px;
        margin-top: 20px;

        >li > a {
            display: block;
            color: #333;
            padding: 10px 15px;

            &:hover{
                background-color: rgba(38,190,201,.05);
                text-decoration: none;
            }
        }

        > li.active a {
            color: $brand-primary;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                top: 50%;
                left: 0px;
                transform: translateY(-50%);
                height: 24px;
                width: 4px;
                background-color: $brand-primary;
            }
        }

        > li.divider {
            height: 1px;
            background-color: #e5e5e5;
        }
    }
}

    