.dropdown-menu{
    top: 100%;
    min-width: 160px;
    padding: 8px;
    margin: 2px 0 0;
    border: none;
    border-radius: 6px;
    box-shadow: 0 6px 12px rgba(0,0,0,.1);

    &:before{
        content: '';
        border-width: 6px;
        border-style: solid;
        border-color: transparent transparent #fff transparent;
        position: absolute;
        top: -12px;
        left: 20px;
    }
   
    &.pull-right:before{
        left: auto;
        right: 20px;
    }

    .divider {
        margin: 8px -8px;
    }

    > li > a {
        padding: 6px 12px;
        clear: both;
        font-weight: normal;
        color: $text-base;
        border-radius: 4px;
        white-space: nowrap; // prevent links from randomly breaking onto new lines

        @media (max-width: $device-sm) {
            font-size: 1.6rem;
            padding:8px 16px;   
        }
    }
}

// Active state
.dropdown-menu > .active > a {
    &,
    &:hover,
    &:focus {
        color: $brand-primary;
        background-color: transparent;
    }
}

.dropdown.sort {
    margin: 14px 0;
    display: inline-block;

    [data-toggle=dropdown] {
        font-weight: bold;
        text-decoration: none;

        i {
            margin-left: 5px;
        }
    }
}