.modal-content {
    border: 0;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
    padding : 10px;   
}

.modal-header {
    border: 0;

    .close {
        font-size: 3rem;
        line-height: 1.2;
    }
}

.modal-title {
    font-size: 2.4rem;
}

.modal-footer {
    border: 0;

    .btn-default {
        border: none;
    }
}