header{
    height: $header-height;
    background-color: $brand-primary;
    padding: $header-padding 0;
    position: fixed;
    width:100%;
    top:0;
    left:0;
    z-index: 99;


    a.logo{
        img{
            height: $header-height - $header-padding * 2;
        }
    }

    ul.menu{
        float: right;
        text-align: right;
        padding: 0;
        list-style: none;

        >li{
            float: right;

            >a{
                display: block;
                border-radius: 4px;
                background: transparent;
                color:#fff;
                line-height: 1;
                text-decoration: none;
                padding: 12px 16px;
                margin: 2px 5px;
                &:hover, &:focus{
                    background: rgba(255,255,255,0.1);
                }
                i {
                    margin-left:5px;
                }
                .user-type{
                    background: rgba(255,255,255,0.3);
                    padding: 2px 8px;
                    margin-right: 4px;
					display: inline;
                }
            }

            >.btn{
                border: 1px solid #fff;
                color:#fff;
            }
        }
    }
}
.menu-backdrop{
    position: fixed;
}

.burger{
    padding: 10px 0 10px 10px;
    margin-top: -3px;
    float: right;
    cursor: pointer;

    .bar{
        background-color: #fff;
        border-radius: 1px;
        width: 25px;
        height: 3px;
        margin-bottom: 5px;
    }
}

@media (max-width: $device-md) {
    header{
        height: $header-mobile-height;
        transition: left 0.3s;

        a.logo img{
            height: $header-mobile-height - $header-padding * 2;
        }
    }

    body:not(.login-body) header{
        ul.menu {
            padding: 10px 20px;
            position: fixed;
            top: 0;
            right: -$header-menu-width-sm;
			width: $header-menu-width-sm;
			height: 100%;
			z-index: 1000;
            background-color: #fff;
			text-align: left;
            box-shadow: 0 0 0 100vw rgba(0,0,0,0);
			transition: 0.3s;
			overflow-y: auto;

            &.active{
                right: 0;
                box-shadow: 0 0 0 100vw rgba(0,0,0,0.4);
            }

            .user-type{
                background: #138184;
                display: inline-block;
            }

            > li {
                display: block;
                float: none;
                margin-top:15px;

                > a {
                    color: $text-base;
                    font-size: $font-size-md;
                    padding: 12px 0;
                }

                > .btn{
                    color: $brand-primary;
                    border-color: $brand-primary;
                    margin-top:15px;
                }

                &.dropdown .dropdown-menu{
                    position: relative;
                    padding-left: 30px;
                    float: none !important;
                    box-shadow: none;

                    &:before{
                        display: none;
                    }
				}
			}

			>li.member-dropdown {
				border-bottom: 1px solid #eee;
				margin: 0 -20px;
				padding: 0 20px;

				.dropdown-menu{
					display: block;
					padding-left: 0;

					>li>a {
						padding: 12px 16px;
						margin: 2px -10px;
						font-size: 1.6rem;
					}
				}
			}
        }
    }
    body.menu-open{
        left: -$header-menu-width-sm;
        cursor: pointer;

        header{
            left: -$header-menu-width-sm;
        }

        .menu-backdrop{
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }

}
@media (max-width: $device-sm) {
	body:not(.login-body) header{
		ul.menu {
			right: -$header-menu-width-xs;
			width: $header-menu-width-xs;
		}
	}

	body.menu-open{
        left: -$header-menu-width-xs;
        cursor: pointer;

        header{
            left: -$header-menu-width-xs;
		}
	}
}
