// footer style
footer{
    background-color: #555;
    padding: 15px 0;
    height: $footer-height;
    @media (max-width: $device-sm){
        height: auto;
        text-align: center;
    }
}
.copyright{
    color:#fff;
    opacity: 0.5;
    font-size: $font-size-sm;
}
ul.footer-link{
    text-align: right;
    margin:0;
    padding:0;
    @media (max-width: $device-sm){
        text-align: center;
        margin: 20px 0;
    }
}
ul.footer-link li{
    display: inline-block;

    a{
        color:#fff;
        font-weight: bold;
        padding:5px;
    }
}