// ------------------------
//Reset Bootstrap Start // 改bootstrap變數如果會動到太多未知的東西就直接寫值

$text-base:             #333 !default;
$text-light:            #666 !default;
$text-lighter:          #999 !default;
$text-muted:            #aaa !default;

$bg-grey-darker:        #ccc !default;
$bg-grey-dark:          #ddd !default;
$bg-grey-light:         #eee !default;
$bg-grey-lighter:       #f4f4f4 !default;
$bg-white:              #fff !default;

$brand-primary:         #138184 !default;
$brand-success:         #138184 !default;
$brand-info:            #26bec9 !default;
$brand-warning:         #FFB400 !default;
$brand-danger:          #EF7A70 !default;

// Scaffoloading
//
$link-color:            $brand-primary !default;
$link-hover-color:      darken($link-color, 10%) !default;
$link-hover-decoration: underline !default;

//== Buttons
//
$btn-default-color:              #333 !default;
$btn-default-bg:                 #fff !default;
$btn-default-border:             #ccc !default;

$btn-primary-color:              #fff !default;
$btn-primary-bg:                 $brand-primary !default;
$btn-primary-border:             $brand-primary !default;

$btn-success-color:              #fff !default;
$btn-success-bg:                 $brand-success !default;
$btn-success-border:             $brand-success !default;

$btn-info-color:                 #fff !default;
$btn-info-bg:                    $brand-info !default;
$btn-info-border:                $brand-info !default;

$btn-warning-color:              #fff !default;
$btn-warning-bg:                 $brand-warning !default;
$btn-warning-border:             $brand-warning !default;

$btn-danger-color:               #fff !default;
$btn-danger-bg:                  $brand-danger !default;
$btn-danger-border:              $brand-danger !default;

$btn-link-disabled-color:        $text-lighter !default;

//== Forms
//
$input-bg:                       #fff !default;
$input-bg-disabled:              $text-lighter !default;
$input-color:                    $text-base !default;
$input-border:                   #e1e1e1 !default;
$input-border-focus:             $brand-primary !default;
$input-color-placeholder:        #aaa !default;

//== Form states and alerts
//
$state-danger-text:              $text-base !default;
$state-danger-bg:                #fff !default;
$state-danger-border:            $brand-danger !default;

//== Font
//
$line-height-base:        1.4;

$font-size-xxxl:          2.4rem;
$font-size-xxl:           2.2rem;
$font-size-xl:            2rem;
$font-size-lg:            1.8rem;
$font-size-md:            1.6rem;
$font-size-base:          1.4rem;
$font-size-sm:            1.3rem;
$font-size-xs:           1.2rem;


//Reset Bootstrap End // 改bootstrap變數如果會動到太多未知的東西就直接寫值
// ------------------------

// ------------------------
//Custom variables start

//== Header
//
$header-padding: 8px;
$header-height: 60px;
$header-mobile-height: 50px;
$footer-height: 45px;
$footer-mobile-height: 107px;

// Menu
$header-menu-width-sm: 300px;
$header-menu-width-xs: 70%;

//== Breakpoints
//
$device-md: 991px;
$device-sm: 767px;
$device-xs: 580px;

//Custom variables end
// ------------------------
